<template>
    <div class="editor">
        <quill-editor 
            v-model="content" 
            ref="myQuillEditor" 
            :option="editorOption" 
            @blur="onEditorBlur($event)"
            @focus="onEditorFocus($event)"
            @ready="onEditorReady($event)">
        >
        </quill-editor>    
    </div>
</template>

<script>
    export default {
        data  () {
            return {
                content:"测试editor",
                editorOption: {}
            }
        },
        mounted:{
            onEditorBlur(quill) {
                console.log('editor blur!', quill)
            },
            onEditorFocus(quill) {
                console.log('editor focus!', quill)
            },
            onEditorReady(quill) {
                console.log('editor ready!', quill)
            },
            
        },
        mounted() {
            
        },
        components: {
            
        }
    }
</script>


